.modalForm{width:100%}@media screen and (min-width: 600px){.modalForm{max-width:500px}}.modalForm .modalForm_text{width:100%;margin-bottom:15px;border-radius:10px;background-color:#fff}.modalForm .modalForm_text label{font-weight:bold}.modalForm .modalForm_text textarea,.modalForm .modalForm_text input{padding:15px;font-size:18px;line-height:1.3;border-radius:10px}@media screen and (min-width: 600px){.modalForm .modalForm_text textarea,.modalForm .modalForm_text input{font-size:20px}}.modalForm .modalForm_text fieldset{border-radius:10px;border:2px solid rgba(0,26,51,.2);box-shadow:0px 4px 4px rgba(0,0,0,.25)}.modalForm .error.modalForm_text{margin-bottom:0}.modalForm .error.modalForm_text fieldset{border-color:red}.modalForm .modalForm_submit{display:block;width:100%;margin-top:15px;padding:0;text-align:center;border-radius:10px}.modalForm .modalForm_submit input{font-weight:bold;font-size:18px;box-sizing:border-box;display:block;width:100%;border-radius:inherit;height:auto;cursor:inherit;padding:13px;color:#fff;background-color:#1976d2;transition:.3s;cursor:pointer}.modalForm .modalForm_submit input:not(:disabled):active{box-shadow:inset 0 0 10px rgba(0,26,51,.5)}.modalForm .modalForm_submit input:not(:disabled):hover{background-color:green}.modalForm .modalForm_submit input:disabled{background-color:#e2e2e2;color:#001a33}@media screen and (min-width: 600px){.modalForm .modalForm_submit{font-size:20px}}.modalForm .modalForm_errorText{position:relative;z-index:1;margin:5px 0 15px;font-size:18px;color:red}.modalForm .modalForm_errorText:before{content:"⚠ ";display:inline}.modalForm{position:relative;z-index:1}